.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 300px;
    height: 120px;
    padding: 20px;
    background-color:#f3f3f3;
    border: 1px solid#dddddd;
    border-radius: 3px;
    margin:auto;
    }

.subtotal__gift {
        display: flex;
        align-items: center;
        }
.subtotal__gift > input {
        margin-right: 5px;
        }
.subtotal > button {
        background: #f0c14b;
        border-radius: 2px;
        width: 100%;
        min-height: 30px;
        border: 1px solid;
        margin-top: 10px;
        border-color: 0#a88734 O#9c7e31 O#846a29;
        color: O#111;
        padding: 2px;
        }